import React from "react"
import ImageAnnotator from "../../components/annotator"
import "toastify-js/src/toastify.css"
import {firebaseConfig, getFiles} from "./file"
import * as firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";

class AllFilesPage extends React.Component {

    state = {images: []}

    componentDidMount() {


        if (typeof window === "undefined") {
            return;
        }

        firebase.initializeApp(firebaseConfig);


        getFiles((files) => this.setState({images: files}), true)
    }

    render() {
        return (!!this.state.images) && this.state.images
            .map((i) => <ImageAnnotator key={i.public_id}
                                        id={i.public_id}
                                        time={null}
                                        image={i}>
                </ImageAnnotator>,
            )
    }
}

export default AllFilesPage

